package strings

import io.github.skeptick.libres.strings.PluralForms
import io.github.skeptick.libres.strings.VoidFormattedString
import io.github.skeptick.libres.strings.VoidPluralString
import io.github.skeptick.libres.strings.getCurrentLanguageCode
import kotlin.String

public object StringsNl : Strings {
  override val byte_symbol: String? = null

  override val kilobyte_symbol: String? = null

  override val megabyte_symbol: String? = null

  override val gigabyte_symbol: String? = null

  override val terabyte_symbol: String? = null

  override val meter_abbreviation: String? = null

  override val kilometer_abbreviation: String? = null

  override val feet_abbreviation: String? = null

  override val mile_abbreviation: String? = null

  override val groupSeparator: String = "."

  override val decimalSymbol: String = ","

  override val time_ago: VoidFormattedString = VoidFormattedString("%1${'$'}s geleden")

  override val time_in_future: VoidFormattedString = VoidFormattedString("over %1${'$'}s")

  override val now: String = "nu"

  override val seconds: VoidPluralString =
      VoidPluralString(PluralForms(one = "seconde", other = "seconden", ), "nl")

  override val seconds_future: VoidPluralString? = null

  override val seconds_past: VoidPluralString? = null

  override val minutes: VoidPluralString =
      VoidPluralString(PluralForms(one = "minuut", other = "minuten", ), "nl")

  override val minutes_future: VoidPluralString? = null

  override val minutes_past: VoidPluralString? = null

  override val hours: VoidPluralString =
      VoidPluralString(PluralForms(one = "uur", other = "uur", ), "nl")

  override val hours_future: VoidPluralString? = null

  override val hours_past: VoidPluralString? = null

  override val days: VoidPluralString =
      VoidPluralString(PluralForms(one = "dag", other = "dagen", ), "nl")

  override val days_future: VoidPluralString? = null

  override val days_past: VoidPluralString? = null

  override val weeks: VoidPluralString =
      VoidPluralString(PluralForms(one = "week", other = "weken", ), "nl")

  override val weeks_future: VoidPluralString? = null

  override val weeks_past: VoidPluralString? = null

  override val months: VoidPluralString =
      VoidPluralString(PluralForms(one = "maand", other = "maanden", ), "nl")

  override val months_future: VoidPluralString? = null

  override val months_past: VoidPluralString? = null

  override val years: VoidPluralString =
      VoidPluralString(PluralForms(one = "jaar", other = "jaar", ), "nl")

  override val years_future: VoidPluralString? = null

  override val years_past: VoidPluralString? = null
}
