  public val byte_symbol: String
    get() = locales[getCurrentLanguageCode()]?.byte_symbol ?: baseLocale.byte_symbol

  public val kilobyte_symbol: String
    get() = locales[getCurrentLanguageCode()]?.kilobyte_symbol ?: baseLocale.kilobyte_symbol

  public val megabyte_symbol: String
    get() = locales[getCurrentLanguageCode()]?.megabyte_symbol ?: baseLocale.megabyte_symbol

  public val gigabyte_symbol: String
