  public val groupSeparator: String
    get() = locales[getCurrentLanguageCode()]?.groupSeparator ?: baseLocale.groupSeparator

  public val decimalSymbol: String
    get() = locales[getCurrentLanguageCode()]?.decimalSymbol ?: baseLocale.decimalSymbol

  public val time_ago: VoidFormattedString
    get() = locales[getCurrentLanguageCode()]?.time_ago ?: baseLocale.time_ago

  public val time_in_future: VoidFormattedString
    get() = locales[getCurrentLanguageCode()]?.time_in_future ?: baseLocale.time_in_future

